nav {
  background-color: #4a00e0;
  padding: 10px;
  width: 50px;
  margin-bottom: 25px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 3px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  transition: width 0.6s linear;
}
nav.active {
  width: 250px;
}
nav ul {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  width: 0;
  transition: width 0.6s linear;
}
nav.active ul {
  width: 100%;
}
nav ul li {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  opacity: 0;
  transition: opacity 0.6s linear, -webkit-transform 0.6s linear;
  transition: transform 0.6s linear, opacity 0.6s linear;
  transition: transform 0.6s linear, opacity 0.6s linear, -webkit-transform 0.6s linear;
}
nav.active ul li {
  opacity: 1;
  -webkit-transform: rotateY(360deg);
          transform: rotateY(360deg);
}
nav ul a {
  font-family: "Bebas Neue", cursive;
  font-size: 20px;
  position: relative;
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
}
nav ul a:hover {
  border-bottom: 2px solid #fff;
}
.icon {
  background-color: #4a00e0;
  border: 0;
  cursor: pointer;
  padding: 0;
  position: relative;
  height: 30px;
  width: 30px;
}
.icon:focus {
  outline: 0;
}
.icon .line {
  background-color: #fff;
  height: 2px;
  width: 20px;
  position: absolute;
  top: 10px;
  left: 5px;
  transition: -webkit-transform 0.6s linear;
  transition: transform 0.6s linear;
  transition: transform 0.6s linear, -webkit-transform 0.6s linear;
}
.icon .line2 {
  top: auto;
  bottom: 10px;
}
nav.active .icon .line1 {
  -webkit-transform: rotate(-765deg) translateY(5.5px);
          transform: rotate(-765deg) translateY(5.5px);
}
nav.active .icon .line2 {
  -webkit-transform: rotate(770deg) translateY(-5.5px);
          transform: rotate(770deg) translateY(-5.5px);
}

@media (max-width: 600px) {
